<template>
 <div>
   <app-header
     showBack="true"
     title="忘记密码"
   ></app-header>
   <div id="forget-warp" style="padding: 100px 20px 200px 20px;">
     <mt-field :attr="{class: 'mint-field-core register-input'}" label="手机号" state="" placeholder="请输入手机号码" v-model="tel">
       <mt-button type="default" size="small" @click="send">发送</mt-button>
       <i style="color: red; font-style: normal;">*</i>
     </mt-field>
     <mt-field :attr="{class: 'mint-field-core register-input'}" label="图形码" v-show="needCode" v-model="vCode">
       <img :src="codeSrc" height="45px" width="100px" @click="refreshVCode ">
       <i style="color: red; font-style: normal;">*</i>
     </mt-field>
     <mt-field :attr="{class: 'mint-field-core register-input'}" label="验证码" state="" placeholder="验证码" v-model="code">
       <i style="color: red; font-style: normal;">*</i>
     </mt-field>
     <mt-field :attr="{class: 'mint-field-core register-input'}" type="password" label="密码" state="" placeholder="请输入密码" v-model="psd">
       <i style="color: red; font-style: normal;">*</i>
     </mt-field>
     <mt-field :attr="{class: 'mint-field-core register-input'}" type="password" label="确认密码" state="" placeholder="确认密码" v-model="passwordTwo">
       <i style="color: red; font-style: normal;">*</i>
     </mt-field>
     <mt-button type="primary" size="large" style="margin-top: 1rem" @click="register">提交</mt-button>
   </div>
 </div>
</template>

<script>
import AppHeader from '../../components/header/header.vue'
import {resetPassword} from '../../api/login'
import {ROOT} from '../../config/urls'
export default {
  name: 'Register',
  data () {
    return {
      tel: '',
      code: '',
      psd: '',
      passwordTwo: '',
      needCode: false,
      vCode: '',
      codeSrc: '',
      t: ''
    }
  },
  mounted () {
    this.$nextTick(() => {
      // 设置当input框为focus状态时输入框置顶，navbar隐藏
      let barEl = document.getElementsByClassName('mint-tabbar')[0]
      let wrapEl = document.getElementById('forget-warp')
      let inputs = document.getElementsByClassName('register-input')
      for (let i = 0, len = inputs.length; i < len; i++) {
        inputs[i].addEventListener('focus', () => {
          wrapEl.style.paddingTop = '50px'
          barEl.style.display = 'none'
        })
        inputs[i].addEventListener('blur', () => {
          wrapEl.style.paddingTop = '100px'
          barEl.style.display = 'flex'
        })
      }
    })
  },
  methods: {
    send () {
      if (!this.tel) return this.$toast('请输入手机号码')
      resetPassword(this.tel, this.vCode)
        .then(data => {
          if (data.state) {
            this.$toast('发送成功，请注意查收')
          } else {
            if (data.errormsg === '图形码不正确') {
              this.$toast('请输入验证码')
              this.needCode = true
              this.codeSrc = ROOT + '/vcode'
            } else {
              this.$toast(data.errormsg)
            }
          }
        })
    },
    refreshVCode () {
      let _random = Math.random() * 100
      this.$set(this, 'codeSrc', ROOT + '/vcode?r=' + _random)
    },
    register () {
      resetPassword(this.tel, this.vCode, this.code, this.psd, this.passwordTwo)
        .then(data => {
          if (data.state) {
            this.$toast('重置成功')
            this.$router.replace('/home')
          } else {
            this.$toast(data.errormsg)
          }
        })
    }
  },
  components: {
    AppHeader
  }
}
</script>

<style lang="less" scoped></style>



