<template>
  <div class="clearfix h-wrap">
    <div class="h-icon" @click="back" v-show="showBack">
      <i class="fa fa-chevron-left" aria-hidden="true"></i>
    </div>
    <div class="h-icon" @click="closeModal" v-show="close">
      <i class="fa fa-angle-left" aria-hidden="true"></i>
    </div>
    <div class="h-title">{{title}}</div>
    <div class="s-icon" @click="sliders" v-show="showSliders">
      <i class="fa fa-sliders" aria-hidden="true"></i>
    </div>
  </div>
</template>

<script>
import bus from '../../assets/js/bus'
export default {
  name: 'App-Header',
  props: {
    title: {type: String},
    showBack: '',
    close: '',
    showSliders: ''
  },
  beforeDestroy () {
    bus.$off('clickCloseModal')
  },
  methods: {
    back () {
      this.$router.back()
    },
    closeModal () {
      bus.$emit('clickCloseModal')
      this.$parent.$emit('clickCloseModalBox')
    },
    sliders () {
      this.$parent.$emit('clickSliders')
    },
    goToOrder () {
    }
  }
}
</script>

<style lang="less" scoped>
  .h-wrap {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    padding: .5rem;
    text-align: center;
    font-size: 1rem;
    border-bottom: 1px solid #eee;
    background-color: cornflowerblue;
    z-index: 10;
    color: white;
    .h-icon{
      position: absolute;
      text-align: center;
      width: 2rem;
    }
    .h-title{
      font-weight: bold;
    }
    .s-icon{
      text-align: center;
      position: absolute;
      right: 0;
      top: .5rem;
      width: 4rem;
      &:after{
        content: "    筛选";
        font-size: 12px;
      }
    }
  }
</style>



